import React, { useState, useEffect } from 'react';
import './mail.css';
function shoot() {
    alert("https://track.mail.shubh.online/api.php?id=" + document.getElementById('myIn').value);
}
function Fme() {
    const [data, setData] = useState([]);
    const getData = () => {
        fetch('https://track.mail.shubh.online/api/'
      ,
        )
            .then(function (response) {
                console.log(response)
                return response.json();
            })
            .then(function (myJson) {
                console.log(myJson);
                setData(myJson)
            });
    }
    useEffect(() => {
        getData()
    }, [])
    // data && data.length>0 && data.map((item)=><p>{item.time}</p>)
    /*<ReactTable
                data={data}
                columns={columns}
                defaultPageSize={2}
                pageSizeOptions={[2, 4, 6]}
            /> */


    return (
        <div >
            <form className="myDD">
                <label>
                    New Tracking : </label>
                <input id="myIn" type="text" name="name" className="myDDin"/>

                <button onClick={shoot}>Generate</button>
            </form>

            <table className="myTab">
                <tr><th>Id</th><th>ip</th></tr>
                {data.map((item) =>
                    <tr>
                        <td><span className="idd">{item.id}</span><br/><span className="time">{item.time}</span></td>
                        
                        <td>{item.ip}</td>
                    </tr>
                )
                }
            </table>

        </div>
    );
}
export default Fme